import { useState } from 'react';
import { authenticate } from '../Config';

function AuthenticationNeeded({ className, setSubmitted }) {
	const [user, setUser] = useState(null);
	const [token, setToken] = useState(null);

	return (
		<div className={className}>
			<h3 className='display-5'>Looks like you refreshed my Page a lot :^)</h3>
			<hr />
				To fetch all my Repos, the frontend sends a Request from your Computer to the GitHub API Server.
				As this Request is not authenticated, you can run out of Request.
				To be able to see my Repos again, we have to send an authenticated Request.
			<hr />
				To do so, you have to provide your username and a generated access token:
			<form>
				<div className='form-group'>
					<label>Username</label>
					<input className='form-control' placeholder='Enter Username' onChange={(e) => setUser(e.target.value)}/>
				</div>
				<div className='form-group'>
					<label>Access Token</label>
					<input type='password' className='form-control' placeholder='Enter Token' onChange={(e) => setToken(e.target.value)}/>
					<small className='form-text text-muted'>I'll never share this with anyone else. I promise!</small>
				</div>
				<button type='submit' className='btn btn-primary' onClick={() => {
					authenticate(user, token);
					setSubmitted();
				}}>Submit</button>
			</form>
		</div>
	);
}

export default AuthenticationNeeded;